import { Value, Block, Node } from '@ali/4ever-cangjie';
import getMatchLevel from './getMatchLevel';

interface IConfig {
  unLimitLevel?: boolean;
  isOrdered?: boolean;
}

export default (
  value: Value,
  currentItem: Block,
  config: IConfig = {},
): Node | null => {
  const { document } = value;
  const level = getMatchLevel(currentItem);
  let prevBlock = document.getPreviousNode(currentItem.key) as Block;
  while (prevBlock) {
    const listConfig = prevBlock?.data?.list;
    if (listConfig && listConfig.level !== undefined) {
      if (config.unLimitLevel || listConfig?.level === level) {
        const isSameType =
          !listConfig.isTaskList && listConfig.isOrdered === config.isOrdered;
        if (config.isOrdered === undefined || isSameType) {
          return prevBlock;
        } else {
          prevBlock = document.getPreviousNode(prevBlock.key) as Block;
          continue;
        }
      }
    }
    prevBlock = document.getPreviousNode(prevBlock.key) as Block;
  }
  return null;
};
